<template>
  <button 
    @click="copyToClipboard" 
    class="text-sm text-primary-600 hover:text-primary-700 flex items-center"
  >
    <img 
      :src="isSuccess ? checkIcon : copyIcon" 
      :alt="isSuccess ? t('copied') : t('copy')" 
      class="w-4 h-4 mr-1" 
    />
    {{ isSuccess ? t('copied') : t('copy') }}
  </button>
</template>

<script setup>
import { ref } from 'vue'

const { t } = useI18n({
  useScope: 'local'
})

const props = defineProps({
  data: {
    type: String,
    required: true
  },
  successDuration: {
    type: Number,
    default: 2000
  }
})

const copyIcon = 'https://unpkg.com/lucide-static@latest/icons/copy.svg'
const checkIcon = 'https://unpkg.com/lucide-static@latest/icons/check.svg'
const isSuccess = ref(false)

const copyToClipboard = async () => {
  try {
    await navigator.clipboard.writeText(props.data)
    isSuccess.value = true
    setTimeout(() => {
      isSuccess.value = false
    }, props.successDuration)
  } catch (e) {
    alert('复制失败')
  }
}
</script>

<i18n lang="json">
{
  "en": {
    "copied": "Copied",
    "copy": "Copy"
  },
  "zh": {
    "copied": "已复制",
    "copy": "复制"
  }
}
</i18n>